const validation = {
  required: [v => !!v || "  "],
  phone: [
    v => !!v || "  ",
    v => (v && v.length > 15) || "   "
  ],
  password: [
    v => !!v || "  ",
    v => (v && v.length > 7) || "     8 "
  ],
  email: [
    v => !!v || " ",
    v => /.+@.+\..+/.test(v) || " E-mail    "
  ]
};

export default {
  install(Vue) {
    Vue.prototype.$validation = validation;
  }
};
